@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body{
  font-family:"Open Sans", sans-serif;
}
.nav-bar{
  display: flex;
  justify-content: space-around;
  background-color: #212121;
}
.tab h2{
  margin: 10px auto;
  padding: 5px;
  color:#FFFFFF;
  cursor: pointer;
}
.tab h2:hover{
  background-color: whitesmoke;
  color:#333333;
}
.container{
  width: 100%;
  margin: 0 auto;
}
.home-content{
  display: flex;
  width: 90%;
  max-width:1500px;
  margin: 30px auto;
  justify-content: space-between;
  align-items: flex-start;
}
.selfie{
  padding-top: 16px;
  max-height: 300px;
}
.intro{
  width:40%;
  max-width: 600px;
  font-size: 500;
  font-size: x-large;
}
.projects{
  margin: 0 auto;
  max-width: 90%;
  padding-top: 16px;
}
.project{
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.project-img{
  max-width:450px;
}
.project-img a img{
  width: 100%;
  border: 2px solid #333333;
}
.project-description{
  padding-left: 16px;
}
.videos{
  display: flex;
  margin: 0 auto;
  max-width: 90%;
  padding-top: 16px;
  align-items: center;
}
.video{ 
  width:100%;
}
.video iframe{
  max-width: 100%;
}
.video-description h4{
  width: 100%;
  max-width: 480px;
  text-align: center;
}
.images-content{
  width: 90%;
  max-width: 2000px;
  margin: 16px auto;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(400px,auto));
  grid-gap: 2rem;
  align-items: flex-start;
}
.image-picture{
  max-width: 90%;
  margin: 0 auto;
  display: block;
  cursor: pointer;
  transition: 0.3s;
}

.image-enlargeView{
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.9);
  flex-direction: column;
  justify-content: center;
}

.image-enlargeContent{
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: 80%;
  max-width: 700px;
}

.back{
	background-color:#FFFFFF;
	border-radius:30px;
	border:1px solid #212121;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:16px 20px;
	text-decoration:none;
  position: fixed;
  visibility: hidden;
  right: 30px;
  bottom: 40px;
}
